.Pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgb(0, 0, 0);
}

.items {
  display: flex;
}

.item {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 29px;
  height: 29px;
  border-radius: 9px;

  &.selected {
    background-color: rgba(19, 18, 24, 1);
    color: white;
  }
}
