.Footer {
  padding: 96px 0 292px;
  background-color: rgba(19, 18, 24, 1);
  display: flex;
  justify-content: center;
  gap: 128px;
  color: white;
  position: relative;
}

.rights {
  position: absolute;
  bottom: 25px;
  left: 50%;
  transform: translateX(-50%);
  font-weight: 500;
  letter-spacing: .4px;
}

.section {
  display: flex;
  flex-direction: column;
}

.title {
  padding-bottom: 16px;
  font-size: 18px;
  font-weight: 900;
  letter-spacing: 1px;
}

.links {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.link {
  font-weight: 500;
  white-space: pre-line;
}

.social-links {
  flex-direction: row !important;
}

@media (max-width: 711px) {
  .Footer {
    flex-direction: column;
    padding: 28px 16px 80px 16px;
    align-items: flex-start;
    gap: 56px;
  }
}
