$width: 288px;

.Products {
  padding-top: 134px;
  padding-bottom: 240px;
  display: flex;
  flex-direction: column;
}

.title {
  text-align: center;
  font-size: 40px;
  font-weight: 700;
  color: rgba(51, 51, 51, 1);
}

.list {
  margin-top: 48px;
  margin-bottom: 85px;
  display: grid;
  gap: 20px;
  justify-content: center;
  grid-template-columns: $width $width $width $width;
}

@media (max-width: 1061px) {
  .list {
    margin-top: 23px;
    grid-template-columns: $width $width;
  }
}

@media (max-width: 594px) {
  .list {
    grid-template-columns: $width;
  }
}
