.AirPods {
  display: flex;
  flex-direction: column;
  gap: 96px;
  color: rgba(51, 51, 51, 1);
  padding-top: 164px;
  padding-bottom: 240px;
}

.block {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 128px;
}

.img-container {

}

.body {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.title {
  font-size: 40px;
  font-weight: 700;
}

.about-text {
  margin-top: 32px;
  white-space: pre-line;
  line-height: 26px;
  font-weight: 500;
}

.contact-btn {
  margin-top: 48px;
  padding: 16px 66px;
  border-radius: 28px;
  color: white;
  background-color: rgba(24, 104, 224, 1);
  font-size: 18px;
  font-weight: 500;
  box-shadow: 0px 0px 16px 0px rgba(152, 44, 205, 0.2);
}

@media (max-width: 600px) {
  .AirPods {
    padding: 58px 16px;
    gap: 80px;
  }
  .block {
    flex-direction: column;
    gap: 48px;
  }
  .body {
    order: 1;
  }
  .title {
    font-size: 24px;
  }
  .about-text {
    margin-top: 28px;
  }
  .contact-btn {
    margin-top: 40px;
  }
  .img-container {
    order: 2;

    img {
      width: 100%;
    }
  }
}
