.PopularModels {
  margin-bottom: 167px;
}

.list {
  margin-bottom: 80px;
  display: grid;
  gap: 24px;
  grid-template-columns: 392px 392px 392px;
  justify-content: center;
}

.title {
  margin-bottom: 40px;
  text-align: center;
  font-size: 40px;
  font-weight: 700;
  color: rgba(51, 51, 51, 1);
}

@media (max-width: 1159px) {
  .PopularModels {
    padding: 0 16px;
  }
  .list {
    grid-template-columns: 392px;
  }
  .title {
    margin-bottom: 24px;
    font-size: 24px;
  }
}

@media (max-width: 401px) {
  .list {
    grid-template-columns: 100%;
  }
}
