.Header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px 108px;
  background-color: #131218;
  color: white;
}

.menu-icon {
  display: none;
  font-size: 30px;
}

.logo {
  font-size: 32px;
  font-weight: 900;
  letter-spacing: 2%;
}

.nav-list {
  display: flex;
  gap: 55px;
  font-weight: 500;
}

.nav-link {
  cursor: pointer;

  &.active {
    color: rgba(24, 104, 224, 1);
  }
}

.nav-link:nth-last-child(1) {
  display: flex;
  gap: 11px;
}

.search-panel {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 12px 15px;
  border-radius: 28px;
  border: 2px solid rgba(24, 104, 224, 1);
}

.search-icon {
  
}

.input {
  font-size: 14px;
  font-weight: 500;
}

.seperator {
  height: 3px;
  background-image: linear-gradient(to right, rgba(24, 104, 224, 1), rgba(180, 204, 240, 1));
}

.side-nav-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.side-nav-link {
  padding-left: 20px;
  font-weight: 500;
}

@media (max-width: 1162px) {
  .Header {
    padding: 11px 12px;
    justify-content: space-between;
  }
  .logo {
    font-size: 24px;
  }
  .nav-list {
    display: none;
  }
  .search-panel {
    display: none;
  }
  .menu-icon {
    display: block;
  }
}
