.PopularModel {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 19px;
  background-color: white;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.06);
  border-radius: 28px;
  padding: 19px 0;
  position: relative;
}

.img-container img {
}

.name {
  font-weight: 700;
  font-size: 22px;
}

.colors {
  display: flex;
  gap: 8px;
}

.color {
  margin-top: 8px;
  width: 16px;
  height: 16px;
  border-radius: 50%;

  &:nth-child(1) {
    background-color: rgba(235, 58, 58, 1);
  }
  &:nth-child(2) {
    background-color: rgba(0, 0, 0, 1);
  }
  &:nth-child(3) {
    background-color: rgba(196, 196, 196, 1);
  }
}

.price {
  margin-top: 16px;
  display: flex;
  gap: 34px;
  align-items: center;
}

.real-price {
  font-size: 26px;
  font-weight: 700;
  color: rgba(225, 35, 80, 1);
}

.price-deleted {
  font-weight: 700;
}

.sale {
  display: grid;
  place-items: center;
  width: 71px;
  height: 26px;
  position: absolute;
  bottom: 24px;
  right: 24px;
  background-color: rgba(24, 191, 111, 1);
  color: white;
  border-radius: 8px;
  font-weight: 600;
}


